}

body {
	background: $softBgColor;
}




// =========================HEADER STYLES==================


header {
	width: 100%;
	background-color: $darkGrey;
	overflow: hidden;

		img {
			height: auto;
			padding: 20px;
			float: left;
			margin: 10px 0px;
		}

		.hamburger {
			float: right;
			margin: 10px 10px;
			padding: 20px;
			cursor: pointer;

			.line-1 {
				width: 45px;
				height: 3px;
				border-radius: 10px;
				background-color: white;
				margin: 6px 0;
				transition: 0.4s; 
			}

			.line-2 {
				width: 45px;
				height: 3px;
				border-radius: 10px;
				background-color: white;
				margin: 6px 0;
				transition: 0.4s;
			}
			
			.line-3 {
				width: 45px;
				height: 3px;
				border-radius: 10px;
				background-color: white;
				margin: 6px 0;
				transition: 0.4s ease-in-out;
			}

			.change.line-1 {
				transform: rotate(-45deg) translate(-5px, 3px);
				width: 45px;
			}
	
			.change.line-2 {
				opacity: 0;
			}
	
			.change.line-3 {
				transform: rotate(45deg) translate(-8px, -8px) ;
			}
	}

	.toggle-nav {
		display: none;
	}

	nav {
		width: 100%;
		background-color: rgba(108, 109, 112, .5);
		clear: both;

		
		ul {
			width: 60%;
			margin: 0px auto;
			padding: 10px;
			display: flex;
			flex-direction: row;
			justify-content: center;
			flex-wrap: wrap;
			

			li {
				margin: 10px;	
			}

			.divider {
				position: relative;
			}

			.divider::after {
				content: "";
				position: absolute;
				height: 12px;
				width: 2px;
				background-color: $textColor;
				top: 20%;
				left: 120%;
			}

			.divider-estate {
				position: relative;
			}

			.divider-estate::after {
				content: "";
				position: absolute;
				height: 12px;
				width: 2px;
				background-color: $textColor;
				top: 20%;
				left: 112%;
			}

			.divider-banking {
				position: relative;
			}

			.divider-banking::after {
				content: "";
				position: absolute;
				height: 12px;
				width: 2px;
				background-color: $textColor;
				top: 20%;
				left: 115%;
			}

			.divider-oil {
				position: relative;
			}

			.divider-oil::after {
				content: "";
				position: absolute;
				height: 12px;
				width: 2px;
				background-color: $textColor;
				top: 20%;
				left: 112%;
			}
			
			a {
				text-decoration: none;
				color: $textColor;
				font-size: 1.1em;
				margin: 0px auto;
			}
		}
	}
}


@media only screen and (max-width : 390px) {

	header{

		img {
			width: 65%;
		}
	}
}




// ==========================HOME STYLES=================




.hero-home {
	width: 100%;	
	margin: auto;
	background-color: #8b8d90;
	background: url("/dist/img/bg-veritas.jpeg") no-repeat center;
	background-size: cover;
	height: 100vh;
	position: relative;


	.text-wrap-home {
		width: 100%;
		padding: 50px;
		position: absolute;
		top: 55%;
		left: 50%;
		transform: translate(-50%, -50%);
		p {
			font-size: 1em;
			text-align: center;
			line-height: 1.3;
			color: $textColor;
			font-family: "TiemposTex-Regular";
		}	
	}

	
}

// ==========================ABOUT STYLES=================

.content-about {
	width: 100%;
	max-width: 1200px;
	padding: 20px;
	margin: auto;
	background-color: $softBgColor;

	.about-blurb {
		text-align: center;
		padding: 20px;
		line-height: 1.3;
		padding: 20px;
	}

	h1 {
		border-bottom: 1px solid $darkGrey;
		text-align: center;
		padding: 10px;
		font-size: 2em;
		color: $darkGrey;
	}

	.col-1-about, .col-2-about, .col-3-about {
		margin-top: 25px;
		text-align: center;
		font-family: "TiemposTex-Regular";
		padding: 20px;


		img {
			width: 70%;
			margin: 20px;
		}

		h2 {
			margin-bottom: 15px;
			font-size: 1.4em;
			color: $darkGrey;
		}

		p {
			font-size: 1.1em;
			margin: 15px 0px;
			line-height: 1.2;
			text-align: left;
		}
	}
}


@media only screen and (min-width : 960px) {

	.content-about {
		min-width: 960px;
		.about-blurb {
			width: 50%;
			margin: auto;
		}

		.col-wrap {
			display: flex;

			.column {
				flex: 1;
				text-align: left;
				margin: 0px;
			}	
			.col-1-about {
				order: 1;
				padding: 0px;
				width: 33.333%;
			}
	
			.col-2-about {
				order: 2;
				padding: 0px;
				width: 33.333%;

			}
			
			.col-3-about {
				order: 3;
				padding: 0px;
				width: 33.333%;

			}
	
			h2 {
				margin: 10px 0px;
			}
	
			p {
				line-height: 1.2;
				font-size: .8em;
			}
			
	
			img {
				width: 90%;
				margin: 0px;
			}
	
		}

		
	}
}

// ==========================TEAM STYLES=================


.content-team {
	width: 100%;
	max-width: 1200px;
	padding: 20px;
	margin: auto;
	background-color: $softBgColor;

	h1 {
		border-bottom: 1px solid $darkGrey;
		text-align: center;
		padding: 10px;
		font-size: 2em;
		color: $darkGrey;
	}

	.col-1-team, .col-2-team, .col-3-team {
		margin-top: 25px;
		text-align: left;
		line-height: 1.2;

		img {
			display: block;
			margin: auto;
			padding: 20px;
		}
		h2 {
			margin: 5px 0px;
			display: inline-block;
			font-size: 1.1em;

		}

		.divider-team {
			position: relative;
		}

		.divider-team::after {
			content: "";
			position: absolute;
			height: 10px;
			width: 2px;
			background-color: $darkGrey;
			top: 2%;
			left: -6%;
		}

		h3 {
			margin: 5px 10px;
			display: inline-block;
			vertical-align: baseline;
			font-size: .7em;
		}
	}
}

